@define-mixin collection {
    height: 24px;
    width: 100%;
    border: 2px solid #adadad;
    margin-top: 10px;
    margin-left: auto;
    margin-right: auto;
    cursor: pointer;
}

.jvmtech-multicolumn .multicolumn-row[data-__neos-insertion-anchor]:empty,
    .jvmtech-multicolumn .multicolumn-col:empty::after {
        height: 24px;
        width: 100%;
        border: 2px solid #adadad;
        margin-top: 10px;
        margin-left: auto;
        margin-right: auto;
        cursor: pointer;
    }

.jvmtech-multicolumn .multicolumn-col:empty::after {
        display: block;
        content: '';
        margin-bottom: 10px;
    }

.jvmtech-multicolumn .multicolumn-break {
        text-align: center;
        padding: 5px 0;
    }
